.card {
  border-radius: 15px;
  margin: 10px;
  cursor: pointer;
  transition: all 100ms ease 0s;
  box-shadow: rgba(0, 0, 0, 0.55) 0px 5px 15px;
}

.card:hover {
  transform: scale(1.1);
}

.pokemon-image {
  width: 7rem;
}

.pokemon-name {
  font-size: 1rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  margin: 0 0 1rem 0;
}

@media (max-width: 480px) {
  .card {
    margin: 4px;
  }
  .pokemon-image {
    width: 6rem;
  }
  .pokemon-name {
    font-size: 0.7rem;
  }
}
